.contacts {
  background: #091c34;

  h2 {
    font-size: 3rem;
    font-weight: 400;
    line-height: 1.167;
    letter-spacing: 0em;
  }

  li {
    font-size: 1rem;
    font-weight: 400;
    line-height: 1.5;
    letter-spacing: 0.00938em;
    padding-top: 8px;
    align-items: center;
    padding-bottom: 8px;
  }
}

.contacts__container {
  flex-direction: column;
  justify-content: center;
}

.contacts__items {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
}

.contacts__separator {
  width: 100%;
  height: 400px;
}

.contacts__img {
  max-width: 15%;
  padding: 100px 0 40px 0;
}

@media (max-width: 540px) {
  img.contacts__img {
    display: none;
  }
}
