.skill-container {
  display: flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  min-width: 170px;
  height: 115px;
}

.skill__block__icon {
  width: 80px;
}

@media (max-width: 686px) {
  .skill-container {
    min-width: 100px;
    flex: 0 1 33%;
  }
}
